.skills-container {
  width: 100%;
  height: max-content;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: start;
  padding-top: 4rem;
  box-sizing: border-box;
}

.skills-list-container {
  width: 100%;
  padding: 0rem 4rem;
  margin-top: 2rem;
  box-sizing: border-box;
}

.skills-name {
  font-size: 1.4rem;
  text-transform: uppercase;
  letter-spacing: 1.1px;
  font-weight: 600;
  color: var(--primary--color);
  animation: slideInFromLeft ease 1s;
}

.skills-horizontal-list {
  width: 100%;
  display: grid;
  gap: 3rem;
  grid-template-columns: repeat(auto-fill, minmax(12rem, 1fr));
  grid-auto-rows: 12rem;
  animation: slideInFromRight ease 1s;
}

.skills-card {
  height: 100%;
  width: 100%;
  background-color: white;
  box-shadow: 0px 2px 6px rgb(206, 206, 206);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  transition: all 100ms;
}

.skills-card:hover {
  cursor: pointer;
  transform: scale(1.06);
}

.skills-image {
  object-fit: contain;
  width: 40%;
  height: 40%;
}

.skills-image-name {
  margin: 0;
  font-size: 1.2rem;
  letter-spacing: 1.1px;
  font-weight: 600;
  margin-top: 1rem;
}

.skills-list-container > hr {
  border: none;
  background-color: rgb(206, 206, 206);
  height: 2px;
  width: 10%;
  margin: 0;
  margin-bottom: 2rem;
}

.spacer {
  margin: 4rem 0rem;
}

@keyframes slideInFromLeft {
  0% {
    opacity: 0;
    transform: translateX(-100%);
  }
  100% {
    opacity: 1;
    transform: translateX(0);
  }
}
@keyframes slideInFromRight {
  0% {
    opacity: 0;
    transform: translateX(100%);
  }
  100% {
    opacity: 1;
    transform: translateX(0);
  }
}
