.qualifications-container {
  width: 100%;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: start;
}

.qualifications-text-container {
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  padding: 0rem 4rem;
}

.qualifications-firstname {
  font-size: 3rem;
  color: var(--primary--color);
  margin: 0;
  text-transform: uppercase;
  letter-spacing: 1.2px;
  animation: slideInFromTop ease 1s;
}

.qualifications-lastname {
  font-size: 2rem;
  margin: 0;
  margin-top: 0.6rem;
  text-transform: uppercase;
  letter-spacing: 1.2px;
  animation: slideInFromBottom ease 1s;
}

.qualifications-card-container {
  display: flex;
  height: 100vh;
  align-items: center;
  justify-content: center;
  flex: 1;
  padding-top: 2rem;
  box-sizing: border-box;
  padding-left: 2rem;
}

.qualifications-card {
  height: 68vh;
  flex: 1;
  background-color: white;
  box-shadow: 0px 4px 10px rgb(187, 187, 187);
  margin-right: 2rem;
  animation: fadeInAnimation ease 3s;
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.qualifications-card.middle {
  height: 72vh;
}

.qualifications-card:hover {
  cursor: pointer;
  transform: translateY(-10px);
}

@keyframes slideInFromTop {
  0% {
    opacity: 0;
    transform: translateY(-100%);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes slideInFromBottom {
  0% {
    opacity: 0;
    transform: translateY(100%);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes fadeInAnimation {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.qualifications-card-image {
  object-fit: contain;
  height: 6rem;
  width: 6rem;
  margin-bottom: 2rem;
}

.qualifications-card > hr {
  width: 90%;
  height: 1px;
  background-color: rgb(204, 204, 204);
  border: none;
}

.qualification-school-name {
  font-size: 1.2rem;
  text-transform: capitalize;
  margin: 0.6rem 0;
  text-align: center;
  letter-spacing: 1.2px;
  color: var(--secondary--color);
  font-weight: 600;
}

.qualification-class {
  margin: 0.6rem 0;
  font-size: 1rem;
}

.qualification-duration {
  margin: 0.6rem 0;
  font-size: 1rem;
}

.qualification-percentage {
  margin: 0.6rem 0;
  font-weight: 600;
  font-size: 1rem;
}

@media only screen and (max-width: 800px) {
  .qualifications-container {
    width: 100%;
    height: max-content;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  .qualifications-container.mobile-container {
    width: 100%;
    height: max-content;
    display: flex;
    flex-direction: column-reverse;
    align-items: center;
    justify-content: center;
  }
  .qualifications-text-container {
    padding: 0;
    padding-top: 5rem;
    padding-bottom: 2rem;
    align-items: center;
  }
  .qualifications-card-container {
    display: flex;
    width: 100%;
    height: max-content;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    box-sizing: border-box;
    padding: 0rem;
  }
  .qualifications-card {
    height: 100vh;
    width: 80%;
    margin-right: 0rem;
    margin-bottom: 1rem;
    padding: 4rem 0rem;
  }
  .qualifications-card.middle {
    height: 100vh;
  }
}
