.home-container {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
  box-sizing: border-box;
}

.home-container-name-container {
  width: 50%;
  display: flex;
  align-items: flex-start;
  flex-direction: column;
  padding-left: 10rem;
  box-sizing: border-box;
  animation: fadeInAnimation ease 3s;
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
}

.home-container-lottie-container {
  width: 50%;
  animation: fadeInAnimation ease 3s;
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
}

@keyframes fadeInAnimation {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.name-container-firstname {
  margin: 0;
  font-size: 3rem;
}

.name-container-lastname {
  margin: 0;
  font-size: 5rem;
  color: var(--primary--color);
}

.name-container-designation {
  margin: 0;
  font-size: 2rem;
  letter-spacing: 10px;
  padding-top: 1rem;
}

.home-container-name-container > hr {
  height: 0.2rem;
  width: 40%;
  background-color: black;
  margin: 1rem 0rem;
}

@media only screen and (max-width: 800px) {
  .home-container {
    width: 100%;
    height: 100vh;
    flex-direction: column-reverse;
    align-items: center;
    justify-content: center;
  }
  .home-container-name-container {
    padding-left: 0rem;
    width: 100%;
    padding: 0rem 2rem;
    align-items: center;
  }
  .home-container-lottie-container {
    width: 100%;
  }
  .name-container-firstname {
    font-size: 2rem;
  }

  .name-container-lastname {
    font-size: 3rem;
  }

  .name-container-designation {
    font-size: 1rem;
    text-align: center;
  }
}
