:root {
  font-size: 16px;
  margin: 0;
  padding: 0;
  --primary--color: #00cfad;
  --secondary--color: #009b81;
  --text--primary--color: #ffffff;
}

a {
  text-decoration: none;
  color: black;
}

body::-webkit-scrollbar {
  width: 0.25rem;
  height: 0.25rem;
}

body::-webkit-scrollbar-track {
  background: #1e1e24;
}

body::-webkit-scrollbar-thumb {
  background: var(--primary--color);
}
