.contact-container {
  width: 100%;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

.contact-container-lottie-container {
  width: 50%;
  animation: fadeInAnimation ease 3s;
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
}

.contact-form-container {
  width: 50%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  padding-left: 6rem;
  box-sizing: border-box;
}

.contact-form-container > input[type="text"],
input[type="email"],
input[type="tel"] {
  width: 24rem;
  height: 3rem;
  padding: 0rem 1rem;
  border: 2px solid var(--secondary--color);
  border-radius: 2px;
  letter-spacing: 1px;
  margin-bottom: 1.4rem;
  animation: slideInFromTop ease 1s;
}

.contact-form-container > textarea {
  width: 24rem;
  height: max-content;
  padding: 1rem 1rem;
  border: 2px solid var(--secondary--color);
  border-radius: 2px;
  letter-spacing: 1px;
  margin-bottom: 1.4rem;
  font-family: inherit;
  animation: slideInFromTop ease 1s;
}

.submit-button {
  height: 3rem;
  width: 26.2rem;
  background-color: rgb(41, 41, 41);
  color: white;
  margin: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.2rem;
  letter-spacing: 1.2px;
  text-transform: uppercase;
  font-weight: 400;
  margin-top: 0.6rem;
  animation: slideInFromBottom ease 1s;
}

.submit-button:hover {
  cursor: pointer;
  background-color: var(--secondary--color);
}

@keyframes fadeInAnimation {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes slideInFromTop {
  0% {
    opacity: 0;
    transform: translateY(-100%);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes slideInFromBottom {
  0% {
    opacity: 0;
    transform: translateY(100%);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

@media only screen and (max-width: 800px) {
  .contact-container {
    width: 100%;
    height: max-content;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  .contact-container-lottie-container {
    width: 100%;
    padding-top: 4rem;
  }
  .contact-form-container {
    width: 80%;
    height: max-content;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 2rem 0rem;
    margin: 0;
  }
  .contact-form-container > input[type="text"],
  input[type="email"],
  input[type="tel"] {
    width: 100%;
  }
  .contact-form-container > textarea {
    width: 100%;
  }

  .submit-button {
    width: 100%;
  }
}
