@import url("https://fonts.googleapis.com/css2?family=Merienda:wght@700&display=swap");

.navbar {
  width: 100%;
  height: 4rem;
  background-color: white;
  box-shadow: 6px 0px 4px gray;
  display: flex;
  align-items: center;
  position: fixed;
  top: 0;
  z-index: 99;
}

.navbar-logo {
  font-size: 1.2rem;
  padding-left: 2rem;
  font-family: "Merienda", cursive;
}

.navbar-items {
  text-decoration: none;
  margin-left: auto;
  padding-right: 2rem;
  display: flex;
  align-items: center;
  justify-content: center;
  box-sizing: border-box;
}

.navbar-items-link {
  height: 4rem;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0 1rem;
  letter-spacing: 0.6px;
}

.navbar-items-link:hover {
  background-color: var(--primary--color);
  color: var(--text--primary--color);
}

.nav-icon {
  font-size: 1.2rem;
  padding-left: 2rem;
  display: none;
}

.nav-icon:hover {
  cursor: pointer;
}

@media only screen and (max-width: 800px) {
  .nav-icon {
    display: block;
  }
  .navbar-logo {
    padding-left: 1rem;
  }

  .navbar-items {
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    position: fixed;
    left: -200vw;
    top: 4rem;
    transition: all 0.3s ease-out;
    background-color: #fff;
    margin: 0;
    padding: 0;
  }
  .navbar-items.active {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: start;
    position: fixed;
    left: 0;
    top: 4rem;
    margin: 0;
    padding: 0;
  }
  .navbar-items-link {
    justify-content: start;
    padding: 0 2rem;
    letter-spacing: 0.6px;
    width: 100%;
  }
}
