.about-container {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-evenly;
  flex: 1;
}

.about-lottie-container {
  display: flex;
  align-items: center;
  flex: 1;
}

.about-text-container {
  display: flex;
  height: 100vh;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  flex: 1;
  padding-top: 6rem;
}

.text-container-heading {
  color: var(--primary--color);
  font-size: 2rem;
}

.about-text-container > hr {
  height: 0.2rem;
  width: 20%;
  background-color: black;
  margin: 0;
  margin-bottom: 1rem;
}

.text-container-paragraph {
  font-size: 1rem;
  line-height: 1.6rem;
  animation: fadeInAnimation ease 3s;
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
  text-align: justify;
  padding-right: 6rem;
}

.text-container-heading {
  position: absolute;
  top: 8rem;
  animation: slideInFromTop ease 1s;
}

@keyframes slideInFromTop {
  0% {
    opacity: 0;
    transform: translateY(-100%);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes fadeInAnimation {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@media only screen and (max-width: 800px) {
  .about-container {
    height: max-content;
    flex-direction: column;
    align-items: center;
    justify-content: start;
  }
  .about-lottie-container {
    margin: 4rem 0rem;
  }
  .text-container-heading {
    position: absolute;
    top: 21rem;
    animation: slideInFromTop ease 1s;
  }
  .about-text-container {
    padding: 0rem 2rem;
  }
  .text-container-paragraph {
    padding-right: 0rem;
  }
}
