@import url(https://fonts.googleapis.com/css2?family=Merienda:wght@700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  font-size: 16px;
  margin: 0;
  padding: 0;
  --primary--color: #00cfad;
  --secondary--color: #009b81;
  --text--primary--color: #ffffff;
}

a {
  text-decoration: none;
  color: black;
}

body::-webkit-scrollbar {
  width: 0.25rem;
  height: 0.25rem;
}

body::-webkit-scrollbar-track {
  background: #1e1e24;
}

body::-webkit-scrollbar-thumb {
  background: #00cfad;
  background: var(--primary--color);
}

.navbar {
  width: 100%;
  height: 4rem;
  background-color: white;
  box-shadow: 6px 0px 4px gray;
  display: flex;
  align-items: center;
  position: fixed;
  top: 0;
  z-index: 99;
}

.navbar-logo {
  font-size: 1.2rem;
  padding-left: 2rem;
  font-family: "Merienda", cursive;
}

.navbar-items {
  text-decoration: none;
  margin-left: auto;
  padding-right: 2rem;
  display: flex;
  align-items: center;
  justify-content: center;
  box-sizing: border-box;
}

.navbar-items-link {
  height: 4rem;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0 1rem;
  letter-spacing: 0.6px;
}

.navbar-items-link:hover {
  background-color: var(--primary--color);
  color: var(--text--primary--color);
}

.nav-icon {
  font-size: 1.2rem;
  padding-left: 2rem;
  display: none;
}

.nav-icon:hover {
  cursor: pointer;
}

@media only screen and (max-width: 800px) {
  .nav-icon {
    display: block;
  }
  .navbar-logo {
    padding-left: 1rem;
  }

  .navbar-items {
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    position: fixed;
    left: -200vw;
    top: 4rem;
    transition: all 0.3s ease-out;
    background-color: #fff;
    margin: 0;
    padding: 0;
  }
  .navbar-items.active {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: start;
    position: fixed;
    left: 0;
    top: 4rem;
    margin: 0;
    padding: 0;
  }
  .navbar-items-link {
    justify-content: start;
    padding: 0 2rem;
    letter-spacing: 0.6px;
    width: 100%;
  }
}

.home-container {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
  box-sizing: border-box;
}

.home-container-name-container {
  width: 50%;
  display: flex;
  align-items: flex-start;
  flex-direction: column;
  padding-left: 10rem;
  box-sizing: border-box;
  -webkit-animation: fadeInAnimation ease 3s;
          animation: fadeInAnimation ease 3s;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

.home-container-lottie-container {
  width: 50%;
  -webkit-animation: fadeInAnimation ease 3s;
          animation: fadeInAnimation ease 3s;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

@-webkit-keyframes fadeInAnimation {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes fadeInAnimation {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.name-container-firstname {
  margin: 0;
  font-size: 3rem;
}

.name-container-lastname {
  margin: 0;
  font-size: 5rem;
  color: var(--primary--color);
}

.name-container-designation {
  margin: 0;
  font-size: 2rem;
  letter-spacing: 10px;
  padding-top: 1rem;
}

.home-container-name-container > hr {
  height: 0.2rem;
  width: 40%;
  background-color: black;
  margin: 1rem 0rem;
}

@media only screen and (max-width: 800px) {
  .home-container {
    width: 100%;
    height: 100vh;
    flex-direction: column-reverse;
    align-items: center;
    justify-content: center;
  }
  .home-container-name-container {
    padding-left: 0rem;
    width: 100%;
    padding: 0rem 2rem;
    align-items: center;
  }
  .home-container-lottie-container {
    width: 100%;
  }
  .name-container-firstname {
    font-size: 2rem;
  }

  .name-container-lastname {
    font-size: 3rem;
  }

  .name-container-designation {
    font-size: 1rem;
    text-align: center;
  }
}

.footer-container {
  width: 100%;
  height: 3rem;
  background-color: var(--primary--color);
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.copyright-text {
  color: var(--text--primary--color);
  letter-spacing: 1px;
  font-weight: 600;
  font-size: 0.8rem;
  padding-left: 2rem;
}

.footer-links-container {
  display: flex;
  align-items: center;
  padding-right: 2rem;
}

.footer-link {
  width: 34px;
  height: 34px;
  border-radius: 50%;
  background-color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1rem;
  margin: 0rem 0.2rem;
}

.footer-link > i {
  color: var(--secondary--color);
}

.footer-link:hover {
  cursor: pointer;
}

@media only screen and (max-width: 800px) {
  .footer-container {
    height: -webkit-max-content;
    height: -moz-max-content;
    height: max-content;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  .copyright-text {
    font-size: 0.8rem;
    padding-left: 0rem;
    padding: 0.6rem 0rem;
  }
  .footer-links-container {
    padding-right: 0rem;
    padding-bottom: 0.6rem;
  }
}

.about-container {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-evenly;
  flex: 1 1;
}

.about-lottie-container {
  display: flex;
  align-items: center;
  flex: 1 1;
}

.about-text-container {
  display: flex;
  height: 100vh;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  flex: 1 1;
  padding-top: 6rem;
}

.text-container-heading {
  color: var(--primary--color);
  font-size: 2rem;
}

.about-text-container > hr {
  height: 0.2rem;
  width: 20%;
  background-color: black;
  margin: 0;
  margin-bottom: 1rem;
}

.text-container-paragraph {
  font-size: 1rem;
  line-height: 1.6rem;
  -webkit-animation: fadeInAnimation ease 3s;
          animation: fadeInAnimation ease 3s;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  text-align: justify;
  padding-right: 6rem;
}

.text-container-heading {
  position: absolute;
  top: 8rem;
  -webkit-animation: slideInFromTop ease 1s;
          animation: slideInFromTop ease 1s;
}

@-webkit-keyframes slideInFromTop {
  0% {
    opacity: 0;
    transform: translateY(-100%);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes slideInFromTop {
  0% {
    opacity: 0;
    transform: translateY(-100%);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

@-webkit-keyframes fadeInAnimation {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes fadeInAnimation {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@media only screen and (max-width: 800px) {
  .about-container {
    height: -webkit-max-content;
    height: -moz-max-content;
    height: max-content;
    flex-direction: column;
    align-items: center;
    justify-content: start;
  }
  .about-lottie-container {
    margin: 4rem 0rem;
  }
  .text-container-heading {
    position: absolute;
    top: 21rem;
    -webkit-animation: slideInFromTop ease 1s;
            animation: slideInFromTop ease 1s;
  }
  .about-text-container {
    padding: 0rem 2rem;
  }
  .text-container-paragraph {
    padding-right: 0rem;
  }
}

.qualifications-container {
  width: 100%;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: start;
}

.qualifications-text-container {
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  padding: 0rem 4rem;
}

.qualifications-firstname {
  font-size: 3rem;
  color: var(--primary--color);
  margin: 0;
  text-transform: uppercase;
  letter-spacing: 1.2px;
  -webkit-animation: slideInFromTop ease 1s;
          animation: slideInFromTop ease 1s;
}

.qualifications-lastname {
  font-size: 2rem;
  margin: 0;
  margin-top: 0.6rem;
  text-transform: uppercase;
  letter-spacing: 1.2px;
  -webkit-animation: slideInFromBottom ease 1s;
          animation: slideInFromBottom ease 1s;
}

.qualifications-card-container {
  display: flex;
  height: 100vh;
  align-items: center;
  justify-content: center;
  flex: 1 1;
  padding-top: 2rem;
  box-sizing: border-box;
  padding-left: 2rem;
}

.qualifications-card {
  height: 68vh;
  flex: 1 1;
  background-color: white;
  box-shadow: 0px 4px 10px rgb(187, 187, 187);
  margin-right: 2rem;
  -webkit-animation: fadeInAnimation ease 3s;
          animation: fadeInAnimation ease 3s;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.qualifications-card.middle {
  height: 72vh;
}

.qualifications-card:hover {
  cursor: pointer;
  transform: translateY(-10px);
}

@-webkit-keyframes slideInFromTop {
  0% {
    opacity: 0;
    transform: translateY(-100%);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes slideInFromTop {
  0% {
    opacity: 0;
    transform: translateY(-100%);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

@-webkit-keyframes slideInFromBottom {
  0% {
    opacity: 0;
    transform: translateY(100%);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes slideInFromBottom {
  0% {
    opacity: 0;
    transform: translateY(100%);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

@-webkit-keyframes fadeInAnimation {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes fadeInAnimation {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.qualifications-card-image {
  object-fit: contain;
  height: 6rem;
  width: 6rem;
  margin-bottom: 2rem;
}

.qualifications-card > hr {
  width: 90%;
  height: 1px;
  background-color: rgb(204, 204, 204);
  border: none;
}

.qualification-school-name {
  font-size: 1.2rem;
  text-transform: capitalize;
  margin: 0.6rem 0;
  text-align: center;
  letter-spacing: 1.2px;
  color: var(--secondary--color);
  font-weight: 600;
}

.qualification-class {
  margin: 0.6rem 0;
  font-size: 1rem;
}

.qualification-duration {
  margin: 0.6rem 0;
  font-size: 1rem;
}

.qualification-percentage {
  margin: 0.6rem 0;
  font-weight: 600;
  font-size: 1rem;
}

@media only screen and (max-width: 800px) {
  .qualifications-container {
    width: 100%;
    height: -webkit-max-content;
    height: -moz-max-content;
    height: max-content;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  .qualifications-container.mobile-container {
    width: 100%;
    height: -webkit-max-content;
    height: -moz-max-content;
    height: max-content;
    display: flex;
    flex-direction: column-reverse;
    align-items: center;
    justify-content: center;
  }
  .qualifications-text-container {
    padding: 0;
    padding-top: 5rem;
    padding-bottom: 2rem;
    align-items: center;
  }
  .qualifications-card-container {
    display: flex;
    width: 100%;
    height: -webkit-max-content;
    height: -moz-max-content;
    height: max-content;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    box-sizing: border-box;
    padding: 0rem;
  }
  .qualifications-card {
    height: 100vh;
    width: 80%;
    margin-right: 0rem;
    margin-bottom: 1rem;
    padding: 4rem 0rem;
  }
  .qualifications-card.middle {
    height: 100vh;
  }
}

.skills-container {
  width: 100%;
  height: -webkit-max-content;
  height: -moz-max-content;
  height: max-content;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: start;
  padding-top: 4rem;
  box-sizing: border-box;
}

.skills-list-container {
  width: 100%;
  padding: 0rem 4rem;
  margin-top: 2rem;
  box-sizing: border-box;
}

.skills-name {
  font-size: 1.4rem;
  text-transform: uppercase;
  letter-spacing: 1.1px;
  font-weight: 600;
  color: var(--primary--color);
  -webkit-animation: slideInFromLeft ease 1s;
          animation: slideInFromLeft ease 1s;
}

.skills-horizontal-list {
  width: 100%;
  display: grid;
  grid-gap: 3rem;
  gap: 3rem;
  grid-template-columns: repeat(auto-fill, minmax(12rem, 1fr));
  grid-auto-rows: 12rem;
  -webkit-animation: slideInFromRight ease 1s;
          animation: slideInFromRight ease 1s;
}

.skills-card {
  height: 100%;
  width: 100%;
  background-color: white;
  box-shadow: 0px 2px 6px rgb(206, 206, 206);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  transition: all 100ms;
}

.skills-card:hover {
  cursor: pointer;
  transform: scale(1.06);
}

.skills-image {
  object-fit: contain;
  width: 40%;
  height: 40%;
}

.skills-image-name {
  margin: 0;
  font-size: 1.2rem;
  letter-spacing: 1.1px;
  font-weight: 600;
  margin-top: 1rem;
}

.skills-list-container > hr {
  border: none;
  background-color: rgb(206, 206, 206);
  height: 2px;
  width: 10%;
  margin: 0;
  margin-bottom: 2rem;
}

.spacer {
  margin: 4rem 0rem;
}

@-webkit-keyframes slideInFromLeft {
  0% {
    opacity: 0;
    transform: translateX(-100%);
  }
  100% {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes slideInFromLeft {
  0% {
    opacity: 0;
    transform: translateX(-100%);
  }
  100% {
    opacity: 1;
    transform: translateX(0);
  }
}
@-webkit-keyframes slideInFromRight {
  0% {
    opacity: 0;
    transform: translateX(100%);
  }
  100% {
    opacity: 1;
    transform: translateX(0);
  }
}
@keyframes slideInFromRight {
  0% {
    opacity: 0;
    transform: translateX(100%);
  }
  100% {
    opacity: 1;
    transform: translateX(0);
  }
}

