.contact-form-container > input[type="text"],
input[type="email"],
input[type="tel"] {
  width: 24rem;
  height: 3rem;
  padding: 0rem 1rem;
  border: 2px solid var(--secondary--color);
  border-radius: 2px;
  letter-spacing: 1px;
