.footer-container {
  width: 100%;
  height: 3rem;
  background-color: var(--primary--color);
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.copyright-text {
  color: var(--text--primary--color);
  letter-spacing: 1px;
  font-weight: 600;
  font-size: 0.8rem;
  padding-left: 2rem;
}

.footer-links-container {
  display: flex;
  align-items: center;
  padding-right: 2rem;
}

.footer-link {
  width: 34px;
  height: 34px;
  border-radius: 50%;
  background-color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1rem;
  margin: 0rem 0.2rem;
}

.footer-link > i {
  color: var(--secondary--color);
}

.footer-link:hover {
  cursor: pointer;
}

@media only screen and (max-width: 800px) {
  .footer-container {
    height: max-content;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  .copyright-text {
    font-size: 0.8rem;
    padding-left: 0rem;
    padding: 0.6rem 0rem;
  }
  .footer-links-container {
    padding-right: 0rem;
    padding-bottom: 0.6rem;
  }
}
